* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  padding-left: 0px;
}

.country-list-item,
.country-info-wraper {
  display: flex;
  align-items: center;
}

.country-list-text,
.country-info-header {
  margin-left: 10px;
}

.country-list-text,
.country-info-text {
    font-weight: 500;
}

.country-info-text-wraper {
  font-weight: bold;
}